import * as actionTypes from "../constants/Productcon";



export const getproductreducer = (state={ products:[] } , action )=>{


    switch(action.type){
        case actionTypes.GET_PRODUCTS_REQUEST:
       return {
           loading: true,
           products: [],
       };
       case actionTypes.GET_PRODUCTS_SUCESS:
           return {
               loading:false,
               products:action.payload,
           };

        case actionTypes.GET_PRODUCTS_FAIL:
            return {

                loading:false,
                error :action.payload,
            }

        default:
            return state;
    }
}


export const getProductsDetailsReducer = (state = { product:{} } , action )=>{


    switch(action.type){
        case actionTypes.GET_PRODUCTS_DETAILS_REQUEST:
       return {
           loading: true,
           
       };
       case actionTypes.GET_PRODUCTS_DETAILS_SUCESS:
           return {
               loading:false,
               product:action.payload,
           };

        case actionTypes.GET_PRODUCTS_DETAILS_FAIL:
            return {

                loading:false,
                error :action.payload,
            };

            case actionTypes.GET_PRODUCTS_DETAILS_RESET:
                
            return { product : {}}

        default:
            return state;
    }
}